import React from "react";
import { observer } from "mobx-react-lite";

import useStore from "../../../hooks/use-store.hook";

import Button from "../../layout/button/button.component";
import preloaderIcon from "../../../resources/icons/preloader-dark.svg";

import { Wrapper, Preloader } from "./show-more.styles";

const ShowMore = () => {
  const { productStore } = useStore();

  const fetchMore = () => {
    productStore.fetchProducts(true);
  };

  if (
    productStore.isDataOver ||
    productStore.products.length === 0 ||
    productStore.isFetching
  ) {
    return <></>;
  }

  return (
    <Wrapper>
      <Button onClick={fetchMore} disabled={productStore.isPaginateFetching}>
        {productStore.isPaginateFetching ? (
          <Preloader src={preloaderIcon} />
        ) : (
          "Показать еще"
        )}
      </Button>
    </Wrapper>
  );
};

export default observer(ShowMore);
